<template>
  <div class="workList">
    <!-- 
        <template>
        <el-table :data="tableData" style="width: 100%">
            <el-table-column
            v-for="column in dynamicColumns"
            :key="column.prop"
            :prop="column.prop"
            :label="column.label">
            </el-table-column>
        </el-table>
        </template>
-->
    <el-table :data="compData" v-loading="loading" border style="width: 100%">
      <el-table-column prop="id" label="用户ID" align="center">
      </el-table-column>
      <el-table-column prop="userId" label="所属班级" align="center">
      </el-table-column>
      <el-table-column prop="title" label="作业名称" align="center">
      </el-table-column>
      <el-table-column prop="completed_text" label="完成情况" align="center">
      </el-table-column>
    </el-table>
    <!-- 分页实现 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 30, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getTableData } from "@/utils/table";
export default {
  data() {
    return {
      tableData: [],

      total: 0,
      currentPage: 1, // 当前页数
      pageSize: 5, // 每页显示的条数
      loading: true,
    };
  },
  created() {
    let tableColumns = document.getElementsByTagName("el-table-column");
    console.log("tableColumns", tableColumns);
    getTableData(this, "/works", {}, ["completed"]);
  },
  computed: {
    compData() {
      // 显示页面数据公式：slice((currentPage - 1) * pageSize, currentPage * pageSize)
      // 公式：从第(currentPage - 1) * pageSize条开始，读取到第 currentPage * pageSize -1 条
      return this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    },
  },
  methods: {
    // 分页实现
    handleSizeChange(val) {
      this.pageSize = val; // 修改每页显示的条数
    },
    handleCurrentChange(val) {
      this.currentPage = val; // 修改当前页数
    },
  },
};
</script>

<style lang="scss">
.workList {
  .demo-form-inline,
  .el-form-item {
    text-align: left;
  }

  .el-pagination {
    margin-top: 20px;
    text-align: left;
  }
}
</style>