// 表格数据获取方法封装
export function getData(root, url, params) {
    root.service.get(url, { params: params || {} })
        .then(res => {
            root.tableData = res.data.data;
            root.total = res.data.total;
        })
        .catch(err => {
            throw err;
        });
}

// 新增和修改方法封装
export function changeInfo(root, method, url, form, callback) {
    root.service[method](url, form)
        .then(res => {
            if (res.data.status === 200) {
                callback(root, url);
                root.dialogFormVisible = false;
                root.$refs.form.resetFields();
                root.$message({ message: res.data.message, type: 'success' });
            }
        })
        .catch(err => {
            throw err;
        });
}

// 删除方法封装
export function delData(root, url, id, callFun) {
    root.$alert('确认删除该条记录吗？', '提示', {
        confirmButtonText: '确定',
        callback: () => {
            root.service.delete(url + '/' + id)
                .then(res => {
                    if (res.data.status === 200) {
                        callFun(root, url);
                        root.$message({ message: res.data.message, type: 'success' });
                    }
                })
                .catch(err => {
                    throw err;
                });
        }
    })
}

// 作业列表获取表格数据方法封装
export function getTableData(root, url, params, arr) {
    root.service.get(url, { params: params || {} })
        .then(res => {
            if (res.data.status === 200) {
                root.tableData = res.data.data;
                root.total = res.data.total;

                // 处理表格数据，给需要转换的字段加上_text后缀
                // arr = ['is_finish']
                // 如：item['is_finish'] = true, item['is_finish_text'] = '已完成'
                root.tableData.map(item => {
                    arr.map(aItem => {
                        item[aItem] ? item[aItem + '_text'] = '已完成' : item[aItem + '_text'] = '未完成';
                    })
                })
                root.loading = false;
            }
        })
        .catch(err => {
            throw err;
        });
}
